.root {
    padding: 0 40px;
}

.container {
    min-height: 50px;
    max-width: 1920px;
    background: #1c1c21;
    position: relative;
}

.linksContainer {
    max-width: 1280px;
    display: flex;
    align-items: center;
    margin: 0 auto;
    padding: 16px 0;
}

.logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.link {
    composes: pr-5 pl-5 pt-4 pb-4 mr-2 from global;

    text-decoration: none;
    display: flex;
    align-items: center;
    border-radius: 40px;
    color: #8585ad;
}

.link_active {
    color: var(--text-primary, #f2f2f3);
}

.link_right {
    margin-left: auto;
}
