.root {
    composes: pt-10 from global;
    max-width: 600px;
    display: flex;
    flex-direction: column;
}

.ingredientsContainer {
    max-height: 750px;
    overflow-y: auto;
}

.title {
    composes: mb-5 from global;
}

.ingredientsList {
    composes: pl-4 pr-4 from global;

    margin-left: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 32px 24px;
}

.link {
    text-decoration: none;
    color: inherit;
}

.burgerIngredient {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.ingredientCost {
    margin: 0 0 4px;
    display: flex;
    align-items: center;
}

.ingredientTitle {
    margin: 0;
    min-height: 48px;
    text-align: center;
}

.tabLink {
    text-decoration: none;
}

.displayFlex {
    display: flex;
}
